.form_block {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.form_block button {
  width: 8rem;
  padding: 1rem 2rem;
  margin-bottom: 1rem;
}
